import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'
const axios = Axios.create({
  baseURL:'http://49.234.48.135:8002/api',
})
axios.defaults.headers.post['Content-Type'] = 'application/json';
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    statisticDataGet(){
      const url = `/statistic/`
      return axios.get(url)
    },
    keywordAnalyzingDataGet(name){
      const url = `/keywordAnalyzing/?name=${name}`
      return axios.get(url)
    },
    paperIntroductionDataGet(){
      const url = `/paperIntroduction/`
      return axios.get(url)
    },
  },
  mutations: {
  },
  actions: {
  },
  modules: {
  }
})
