<template>
  <div style="">
    <div
      :style="{ backgroundImage: `url(${mainBackgroundImageUrl})` }"
      class="mainBackgroundImage"
    >
      <div
        :style="{ backgroundImage: `url(${mainBackgroundTitleUrl})` }"
        class="mainBackgroundTitle"
      >
        <div class="statistic" @click="goSubmenu('statistic')" @mouseover="mouseoverStyle($event)" @mouseout="mouseoutStyle($event)">Statistic</div>
        <div class="index" @click="goSubmenu('index')" @mouseover="mouseoverStyle($event)" @mouseout="mouseoutStyle($event)">I N D E X</div>
        <div class="keywordAnalysis" @click="goSubmenu('keywordAnalysis')" @mouseover="mouseoverStyle($event)" @mouseout="mouseoutStyle($event)">Keyword<br />Anakysis</div>
        <div class="paperIntroduction" @click="goSubmenu('paperIntroduction')" @mouseover="mouseoverStyle($event)" @mouseout="mouseoutStyle($event)">Paper<br />Introduction</div>
        <div class="message" @click="goSubmenu('message')" @mouseover="mouseoverStyle($event)" @mouseout="mouseoutStyle($event)">Message</div>
        <div class="discussion" @click="goSubmenu('discussion')" @mouseover="mouseoverStyle($event)" @mouseout="mouseoutStyle($event)">Discussion</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "mainMenu",
  data() {
    return {
      data: [],
      selectKeys: [],
      mainBackgroundImageUrl: "",
      mainBackgroundTitle: "",
      mainBackgroundTitleUrl: "",
    };
  },
  computed: {
    url() {
      return this.$route.name;
    },
  },
  watch: {
    url() {
      this.selectKeys = [this.$route.name];
    },
  },
  methods: {
    goSubmenu(value) {
      if(value != this.selectKeys[0]){
          this.$router.push("/" + value);
        }
    },
    mouseoverStyle($event){
      $event.target.className = $event.target.className + ' mouseoverStyle';
    },
    mouseoutStyle($event){
      let className = $event.target.className;
      className = className.split(" mouseoverStyle")[0]
      $event.target.className = className
    },
  },
  beforeMount(){
    this.selectKeys = [this.$route.name];
  },
  mounted() {
    this.mainBackgroundImageUrl = require("../assets/mainBackgroundImage.png");
    this.mainBackgroundTitleUrl = require("../assets/mainBackgroundTitle.png");
  },
};
</script>

<style scoped>
.mainBackgroundTitle {
  background-position: center top;
  background-repeat: no-repeat;
  width: 100%;
  height: 720px;
  background-size: 1000px;
}
.mainBackgroundImage {
  background-position: center top;
  width: 100%;
  height: 720px;
  background-size: 1200px;
}
.statistic {
  font-size: 45px;
  font-family: -apple-system;
  font-weight: lighter;
  line-height: 210px;
  width: 280px;
  height: 210px;
  background: rgba(223, 165, 233, 0.72);
  position: absolute;
  left: 20%;
  top: 30%;
  cursor: pointer;
  transition: all .9s;
}
.index {
  font-size: 50px;
  font-weight: bold;
  line-height: 300px;
  width: 400px;
  height: 300px;
  background: radial-gradient(rgba(165,219,242,0.95),rgba(67, 150, 255,0.62));
  position: absolute;
  left: 50%;
  top: 27%;
  margin-left: -200px;
  z-index: 8000;
  cursor: pointer;
  transition: all .9s;
}
.keywordAnalysis {
  font-size: 40px;
  font-family: -apple-system;
  font-weight: lighter;
  line-height: 60px;
  padding-top: 45px;
  width: 280px;
  height: 210px;
  background: rgba(255,206,206,0.65);
  position: absolute;
  left: 61.5%;
  top: 30%;
  cursor: pointer;
  transition: all .9s;
}
.paperIntroduction {
  font-size: 40px;
  font-family: -apple-system;
  font-weight: lighter;
  line-height: 60px;
  padding-top: 45px;
  width: 280px;
  height: 210px;
  background: rgba(232, 209, 130, 0.72);
  position: absolute;
  left: 20%;
  top: 58.6%;
  cursor: pointer;
  transition: all .9s;
}
.message {
  font-size: 45px;
  font-family: -apple-system;
  font-weight: lighter;
  line-height: 250px;
  width: 380px;
  height: 210px;
  background: rgba(119, 249, 130, 0.35);
  position: absolute;
  left: 50%;
  top: 58.6%;
  margin-left: -190px;
  cursor: pointer;
  transition: all .9s;
  z-index:7000;
}
.discussion {
  font-size: 45px;
  font-family: -apple-system;
  font-weight: lighter;
  line-height: 210px;
  width: 280px;
  height: 210px;
  background: rgba(166, 198, 239, 100);
  position: absolute;
  left: 61.5%;
  top: 58.6%;
  cursor: pointer;
  transition: all .9s;
}
.mouseoverStyle {
  transform: scale(1.1);
}
</style>