<template>
  <div
    :style="{ backgroundImage: `url(${subBackgroundImageUrl})` }"
    class="subBackgroundImage"
  >
      <div style="padding-top:30px;font-size:20px;">{{this.name}}</div>
      <a-table :columns="columns" :data-source="data" :rowKey="'id'" v-show="isTableShow"></a-table>
  </div>
</template>

<script>
const columns = [
  {
    title: "",
    dataIndex: "content",
    
  },
  {
    title: "",
    dataIndex: "classification",
    filters: [
      {
        text: "positive",
        value: "positive",
      },
      {
        text: "negative",
        value: "negative",
      },
      {
        text: "neutral",
        value: "neutral",
      },
    ],
    onFilter: (value, record) => record.classification.includes(value),
  },
  {
    title: "",
    dataIndex: "confidenceCoefficient",
    sorter: (a, b) => a.confidenceCoefficient > b.confidenceCoefficient,
  },
  {
    title: "",
    dataIndex: "positive",
    sorter: (a, b) => a.positive > b.positive,
  },
  {
    title: "",
    dataIndex: "negative",
    sorter: (a, b) => a.negative > b.negative,
  },
]
export default {
  name: "keywordAnalyzing",
  data() {
    return {
      subBackgroundImageUrl: "",
      isTableShow:false,
      data: [],
      name:this.$route.query.name,
      columns,
    };
  },
  methods: {
    getBackendData() {
      this.$store.state.keywordAnalyzingDataGet(this.name).then((response) => {
        this.data = response.data;
        this.isTableShow = true
      });
    },
  },
  mounted() {
    this.subBackgroundImageUrl = require("../assets/subBackgroundImage.png");
    this.getBackendData()
    
  }
};
</script>

<style scoped>
.subBackgroundImage {
  background-position: center top;
  width: 100%;
  height: 720px;
  background-size: 1200px;
}
</style>