<template>
  <div
    :style="{ backgroundImage: `url(${subBackgroundImageUrl})` }"
    class="subBackgroundImage"
  >
      <div style="padding-top:20px;padding-bottom:15px;font-size:20px;"></div>
      <a-table
        v-show="isTableShow"
        :columns="columns"
        :data-source="data"
        :rowKey="'id'"
        :expandedRowKeys="expandedRowKeys"
        @expand="expand"
      >
        <div
          slot="filterDropdown"
          slot-scope="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          style="padding: 8px"
        >
          <a-input
            :placeholder="`Search ${column.dataIndex}`"
            :value="selectedKeys[0]"
            style="width: 188px; margin-bottom: 8px; display: block"
            @change="
              (e) => setSelectedKeys(e.target.value ? [e.target.value] : [])
            "
            @pressEnter="
              () => handleSearch(selectedKeys, confirm, column.dataIndex)
            "
          />
          <a-button
            type="primary"
            icon="search"
            size="small"
            style="width: 90px; margin-right: 8px"
            @click="() => handleSearch(selectedKeys, confirm, column.dataIndex)"
            >Search</a-button
          >
          <a-button
            size="small"
            style="width: 90px"
            @click="() => handleReset(clearFilters)"
            >Reset</a-button
          >
        </div>
        <a-icon
          slot="filterIcon"
          slot-scope="filtered"
          type="search"
          :style="{ color: filtered ? '#108ee9' : undefined }"
        />

        <a-table
          slot="expandedRowRender"
          :columns="innerColumns"
          :data-source="innerData"
          :rowKey="'id'"
          :pagination="false"
        >
          <a
            :href="record.url"
            target="_blank"
            slot="url"
            slot-scope="text, record"
            >{{ text }}</a
          >
        </a-table>
      </a-table>
  </div>
</template>

<script>
const columns = [
  {
    title: "",
    dataIndex: "theme",
    scopedSlots: {
      filterDropdown: "filterDropdown",
      filterIcon: "filterIcon",
    },
    onFilter: (value, record) => {
      return record.theme.includes(value);
    },
  },
  {
    title: "",
    dataIndex: "author",
    scopedSlots: {
      filterDropdown: "filterDropdown",
      filterIcon: "filterIcon",
    },
    onFilter: (value, record) => {
      return record.author.includes(value);
    },
  },
  {
    title: "",
    dataIndex: "keyword",
    scopedSlots: {
      filterDropdown: "filterDropdown",
      filterIcon: "filterIcon",
    },
    onFilter: (value, record) => {
      return record.keyword.includes(value);
    },
  },
];
const innerColumns = [
  {
    title: "",
    dataIndex: "url",
    scopedSlots: { customRender: "url" },
  },
  {
    title: "()",
    dataIndex: "abstract",
    ellipsis: true,
  },
];
export default {
  name: "paperIntroduction",
  data() {
    return {
      subBackgroundImageUrl: "",
      isTableShow:false,
      data: [],
      innerData: [],
      expandedRowKeys: [],
      columns,
      innerColumns,
    };
  },
  methods: {
    expand(expanded, record) {
      //console.log(expanded,record)
      this.expandedRowKeys = [];
      if (expanded) {
        this.innerData = [];
        this.expandedRowKeys = [record.id];
        this.innerData.push(this.data.find((item) => item.id == record.id));
      }
    },
    getBackendData() {
      this.$store.state.paperIntroductionDataGet(this.name).then((response) => {
        this.data = response.data;
        this.isTableShow = true
      });
    },
    handleSearch(selectedKeys, confirm, dataIndex) {
      //console.log(selectedKeys, confirm, dataIndex)
      confirm();
    },
    handleReset(clearFilters) {
      clearFilters();
    },
  },
  mounted() {
    this.subBackgroundImageUrl = require("../assets/subBackgroundImage.png");
    this.getBackendData();
  },
};
</script>

<style scoped>
.subBackgroundImage {
  background-position: center top;
  width: 100%;
  height: 720px;
  background-size: 1200px;
}
</style>