import Vue from 'vue'
import VueRouter from 'vue-router'
import mainMenu from "../views/mainMenu"
import index from "../views/index"
import statistic from "../views/statistic"
import keywordAnalysis from "../views/keywordAnalysis"
import paperIntroduction from "../views/paperIntroduction"
import message from "../views/message"
import discussion from "../views/discussion"



Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect:'/mainMenu',
    component: mainMenu
  },
  {
    path:'/mainMenu',
    name:"mainMenu",
    component:mainMenu,
  },
  {
    path:'/index',
    name:"index",
    component:index,
  },
  {
    path:'/statistic',
    name:"statistic",
    component:statistic,
  },
  {
    path:'/keywordAnalysis',
    name:"keywordAnalysis",
    component:keywordAnalysis,
  },
  {
    path:'/paperIntroduction',
    name:"paperIntroduction",
    component:paperIntroduction,
  },
  {
    path:'/message',
    name:"message",
    component:message,
  },
  {
    path:'/discussion',
    name:"discussion",
    component:discussion,
  },

]

const router = new VueRouter({
  routes
})

export default router
